:root {
  --bkg-color-1: black;
  --bkg-color-2: maroon;
  --bkg: white;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  scroll-behavior: smooth;
}

body {
  background-color: var(--bkg);
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

img {
  display: block;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.btn, .input {
  appearance: none;
  height: 32px;
  border: none;
  padding: 8px 15px;
  display: flex;
}

.bottom {
  height: 100px;
  color: #545454;
  background-color: #f7f7f7;
}

.hidden {
  display: none;
}

.header {
  z-index: 99;
  width: 100%;
  height: 216px;
  background-color: var(--bkg-color);
  background-image: linear-gradient(to right, var(--bkg-color-1), var(--bkg-color-2));
  color: #fff;
  padding: 20px 0;
  display: flex;
  position: fixed;
  top: 0;
}

.container-header {
