  justify-content: center;
  align-items: center;
  display: flex;
}

#search-input {
  width: 240px;
  height: 32px;
  color: #fff;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
}

#search-input:focus-within {
  border: 1px solid red;
  box-shadow: 0 0 10px red;
}

